import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RegistersService } from '../../shared/service/loginapi/register.service';
import { AuthService } from "angular4-social-login";
import { SocialUser } from "angular4-social-login";
import { debuglog } from 'util';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class HomeComponent implements OnInit {
  private user: SocialUser;
  private loggedIn: boolean;

  constructor(public router: Router, public _RegistersService: RegistersService, private authService: AuthService) { }

  ngOnInit() {
  debugger
    this.authService.authState.subscribe((user) => {
      this.user = user;
      this.loggedIn = (user != null);
    });

  }
  LogOut() {
    this.router.navigateByUrl('login');
  }
  // GetUser()
  // {
  //   this._RegistersService.GetUserDetails('rest-auth/user/').subscribe(data=>{console.log(data)})
  // }
}



